var staticRenderFns = [function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"details"},[_c('div',{staticClass:"signer"},[_c('p',[_vm._v("甲方：")]),_c('p',[_vm._v("乙方：")])]),_c('p',[_vm._v("\n            甲乙双方为携手合作，促进发展，满足利益，明确责任，依据《中华人民共和国合同法》有关法律之相关规定，本着诚实信用，互惠互利原则，结合双方实际，协商一致，特签订本合同，以求共同恪守："),_c('br'),_vm._v("\n            第一条：甲方委托乙方生产加工制作________________。"),_c('br'),_vm._v("\n            第二条：质量标准和要求、验收方法（标准）及地点："),_c('br'),_vm._v("\n            1、质量标准和要求：模治檢具生产的质量标准按甲方的质量要求执行。"),_c('br'),_vm._v("\n            （1）乙方必须按甲方提供的模具技术要求设计、制造模治檢具。"),_c('br'),_vm._v("\n            （2）合格模治檢具定义："),_c('br'),_vm._v("\n            ①必须符合经甲方提供确认的结构图；"),_c('br'),_vm._v("\n            ②必须生产出符合甲方提供的正式零件图纸要求；"),_c('br'),_vm._v("\n            ③若有組裝需求，必須生產出完全符合甲方要求的組裝模治檢具；"),_c('br'),_vm._v("\n            2、验收方法（标准）："),_c('br'),_vm._v("\n            ①按甲方的图纸和技术要求执行。"),_c('br'),_vm._v("\n            ②结构必须符合模治檢具零件结构图纸。"),_c('br'),_vm._v("\n            3、验收地点：在甲方指定地点________________进行。每次验收必须有双方签署的验收报告，如验收不合格，必须在验收报告上注明下次验收期限。"),_c('br'),_vm._v("\n            第三条：原材料来源"),_c('br'),_vm._v("\n            1、原材料来源：模治檢具制造生产所需的材料由乙方承担。乙方必须保证按甲方提供的技术要求采购原材料，以确保产品质量的稳定性，若乙方偷工减料或以次充好，甲方可单方面终止合同；材料由甲方提供的，乙方无需承担材料相关的法律责任。"),_c('br'),_vm._v("\n            第四条：交（收）货方法、地点及运杂费负担："),_c('br'),_vm._v("\n            1、交货方法及验收：本合同模治檢具在乙方按甲方提供的图纸技术要求进行试模、送样，并经双方确认签板后视为模具验收合格。"),_c('br'),_vm._v("\n            2、交货地点：甲方指定场所________________________。"),_c('br'),_vm._v("\n            3、交货时间：乙方模具生产自合同簽定之日起______天內交付本合同模治檢具。否则，乙方必须承担延期交货给甲方造成的损失（每延遲交貨1日，罚款200元）。如是甲方原因造成不能如期交货的，经甲方书面形式确认后，交付时间可以按延误时间顺延。"),_c('br'),_vm._v("\n            4、运杂费用承担：由乙方送货到甲方指定地点，运杂费由甲方承担。"),_c('br'),_vm._v("\n            第五条：本合同模治檢具总金额为_______万元，共_______套模治檢具。"),_c('br'),_vm._v("\n            货款结算时间及方法：_____________________。"),_c('br'),_vm._v("\n            第六条：技术资料提供及保管："),_c('br'),_vm._v("\n            1、甲方负责提供本合同约定的模具的图纸、技术要求及有关标准。"),_c('br'),_vm._v("\n            2、涉及到资料中技术秘密的保管和有关約定并承擔技術洩密法律責任。"),_c('br'),_vm._v("\n            第七条：质量异议/违约责任："),_c('br'),_vm._v("\n            1、对于乙方供应的产品如存在质量问题，甲方必须以书面的形式将质量事故状况通知乙方，并对乙方供应的产品进行妥善保管；乙方在接到甲方书面通知24小时内出具解决方案并赶甲方现场进行处理。"),_c('br'),_vm._v("\n            2、如甲方未按上款要求向乙方提出异议，则视为乙方供应之产品为合格品；如乙方未按上款要求保证进行售后服务，则视为接受甲方的处理意见。"),_c('br'),_vm._v("\n            3、其他涉及到产品质量问题的雙方可以通過協商解決，協商解決不了的通過司法程序解決。"),_c('br'),_vm._v("\n            第八条：其他："),_c('br'),_vm._v("\n            本合同委托项目，乙方不得转发包给第三者，否则，甲方将取消其其加工资格，如因此引发了甲方损失，甲方将依法追究乙方相关责任。"),_c('br'),_vm._v("\n            第九条：双方由于不可抗力的灾害和确非一方本身造成的原因而不能履行合同时，经双方协商并由___________市经济合同鉴定机关查实证明，可免予承担民事责任。"),_c('br'),_vm._v("\n            第十条：合同生效、附件"),_c('br'),_vm._v("\n            1、本合同自签订之日起，双方签字之后生效。"),_c('br'),_vm._v("\n            2、任何一方不得擅自修改或终止，如需修改或终止时，应经双方协商同意，另立协议方可有效。本合同权利不可转让。"),_c('br'),_vm._v("\n            第十一条：本合同在有效期内任何一方违约，双方都有权按《中华人民共和国合同法》有关规定，由双方进行协商调解。协商未果的，均向甲方所在地人民法院诉讼解决。"),_c('br'),_vm._v("\n            第十二条：本合同一式两份，双方各执一份。"),_c('br')]),_c('div',{staticClass:"sign"},[_c('div',[_c('p',[_vm._v("甲方：")]),_c('p',[_vm._v("委託代理人：")]),_c('p',[_vm._v("地址：")]),_c('p',[_vm._v("電話：")]),_c('p',[_vm._v("_________年____月____日")])]),_c('div',[_c('p',[_vm._v("乙方：")]),_c('p',[_vm._v("委託代理人：")]),_c('p',[_vm._v("地址：")]),_c('p',[_vm._v("電話：")]),_c('p',[_vm._v("_________年____月____日")])])])])}]

